<template>
  <div class="discoverDetail">
    <div class="box" ref="box">
      <div class="title">{{obj.textname}}</div>
      <div class="promulgator">
        <p>{{obj.authorname}}</p>
        <p
          v-if="new Date(obj.starttime).toLocaleDateString()==new Date().toLocaleDateString()?true:false"
        >{{new Date(obj.starttime).getHours().toString().padStart(2,0)}}:{{new Date(obj.starttime).getMinutes().toString().padStart(2,0)}}</p>
        <p
          v-if="new Date(obj.starttime).toLocaleDateString()==new Date().toLocaleDateString()?false:true"
        >{{new Date(obj.starttime).getFullYear().toString().padStart(2,0)}}/{{(new Date(obj.starttime).getMonth()+1).toString().padStart(2,0)}}/{{new Date(obj.starttime).getDate().toString().padStart(2,0)}}</p>

      </div>
      <p class="content_con" v-for="(obj,index) of arr" :key="index">{{obj.trim()}}</p>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      obj: this.$route.query.obj,
      arr: [],
    };
  },
  created() {
    this.$nextTick(() => {
      this.arr = this.obj.articleContent.split("&");
      setTimeout(() => {
        this.init();
      }, 0);
    });
  },
  methods: {
    init() {
      var Op = document.getElementsByClassName("content_con");
      let array = Array.from(Op);
      for (let item of array) {
        let id = item.innerHTML.substring(0, 2);
        let img = item.innerHTML.substring(2);
        if (id === "$$") {
          let imgUrl = this.obj.textpath + img;
          item.innerHTML =
            "<img style='width:100%;height:2rem;margin:.1rem 0 0 -0.32rem' :src=" +
            imgUrl +
            "/>";
        }
      }
    },
  },
};
</script>
<style lang="less" scoped>
.discoverDetail {
  .box {
    padding: 0.6rem 0.2rem 1.2rem 0.2rem;
    box-sizing: border-box;
    background: #fff;
  }
  .title {
    width: 100%;
    height: 0.3rem;
    line-height: 0.3rem;
    font-size: 0.18rem;
    font-weight: bold;
  }
  .promulgator {
    width: 100%;
    height: 0.28rem;
    font-size: 0.14rem;
    color: #969696;
    p:first-of-type {
      float: left;
      color: #45A4B1;
    }
    p:last-of-type {
      float: left;
      margin-left: 0.1rem;
    }
  }
  .content_con {
    font-size: 0.16rem;
    text-indent: 0.32rem;
    margin-top: .15rem;
  }
}
</style>